import React, { Component } from 'react';
import { connect } from 'react-redux';
import { PulseLoader } from 'react-spinners';
import * as actions from '../actions';
import { getParameterByName, loginStatus } from '../util';
import NavBar from './NavBar';
import SideBar from '../containers/SideBar';
import Modal from '../containers/Modal';
import Tabs from '../containers/Tabs';
import Main from '../containers/Main';
import * as am4core from "@amcharts/amcharts4/core";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4themes_dark from "@amcharts/amcharts4/themes/dark";
// css entry point
import variables from '../styles/variables.scss';
import '../styles/main.scss';
am4core.useTheme(am4themes_dark);
//am4core.useTheme(am4themes_animated);
am4core.options.autoSetClassName = true;
class App extends Component {
  state = {
    loading: true
  }

  async componentDidMount() {
    const status = await loginStatus();
    await this.props.loadDashboard()
    .then(() => this.setState({loading: false}))
    .catch((e) => {
      console.log(e);
      this.setState({loading: 'failed'});
    });
    if(getParameterByName('mode') !== 'builder') {
      this.props.toggleViewOnly();
    }
    await this.props.currentData();
    await this.props.assetData();
    await this.props.attributes();
    await this.props.assetDefinitionData();

    this.interval = setInterval(() => this.props.currentData(), 5000);
  }

  render() {
    const { loading } = this.state;
    if(loading === 'failed') {
      return ( < >
        <NavBar toggleSidebar={this.toggleSidebar} />
        <section style={{height:'94vh'}}>
          <div style={{position: 'absolute', top: '50%', left:'50%', transform: 'translateX(-50%)'}}>
            <h2 >Failed to load Dashboard. Please refresh.</h2>
          </div>
        </section>
        </>
      )
    } else if (loading) {
      return ( < >
        <NavBar toggleSidebar={this.toggleSidebar} />
        <section style={{height:'94vh'}} >
          <div style={{position: 'absolute', top: '50%', left:'50%', transform: 'translateX(-50%)'}} >
            <PulseLoader
              sizeUnit={'px'}
              size={20}
              color={variables.fontColor}
            />
          </div>
        </section>
        </>
      )
    }
    return (
      < >
        <NavBar toggleSidebar={this.toggleSidebar} />
        <SideBar className='sidebar' />
        <Modal />
        <Tabs />
        <Main className='main'/>
      </>
    );
  }
}


export default connect(null, actions)(App);
