const activeTiles = (state=[], action) => {
  switch(action.type) {
    case 'ADD_TILE':
      return [
        ...state,
        {
          id: action.id,
          properties: action.tile.properties,
          dataGrid: action.tile.dataGrid
        }
      ];
    case 'DELETE_TILE':
      return state.filter(tile => tile.id !== action.id)
    case 'LOAD_DASHBOARD':
      if(action.payload.data.tiles) {
        return action.payload.data.tiles[0];
      } else {
        return state;
      }
    case 'SET_ACTIVE_TILES':
      if(action.tiles) {
        return action.tiles
      } else {
        return [];
      }

    default:
      return state;
  }
}

export default activeTiles;
