import React, { Component } from 'react';
import { connect } from 'react-redux';
import { InputText } from 'primereact/inputtext';
import { Dropdown } from 'primereact/dropdown';
import { ColorPicker } from 'primereact/colorpicker';

class CylinderGaugeForm extends Component {
  state = {
    dataItem: null,
    min : '',
    max: '',
    unit: '',
    fillColor: ''
  }

  dataItems = this.props.data.dataItems;

  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem, name, min, max, unit, band1, band2, band3 } = tile.properties;
      const newDi = this.dataItems.filter((di) => di.label === dataItem.label);
      this.setState({
        dataItems: this.dataItems,
        dataItem: newDi[0],
        name,
        min,
        max,
        unit,
        band1,
        band2,
        band3
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItem, max, unit, fillColor } = this.state;
    const { dataItems } = this;
    return (
      <div className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={dataItems}
          placeholder='Select a data item'
          filter={true}
          required
        />
      <span className='p-float-label'>
        <InputText
          value={max}
          type='number'
          onChange={this.handleChange}
          name='max'
          required
        />

      <label htmlFor='max'>Max*</label>
      </span>
      <span className='p-float-label'>
        <InputText
          value={unit}
          type='text'
          onChange={this.handleChange}
          name='unit'
        />
      <label htmlFor='unit'>Unit</label>
        </span>
        <div className='color-picker'>
          <span className='color-picker-label'>Fill Color:</span>
          <ColorPicker
            value={fillColor}
            name='fillColor'
            onChange={this.handleChange}
            defaultColor='000000'
          />
        <span className='color-picker-color'>{fillColor}</span>
        </div>
        <p style={{fontSize: '12px', marginTop: 0}}>
          {'Fill color will default if not specifically selected'}
        </p>
      </div>

    );
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(CylinderGaugeForm);
