import React, { Component } from 'react'
import { connect } from 'react-redux';
import PropTypes from 'prop-types'

class Indicator extends Component {
  state = {}
  static defaultProps = {}

  render () {
    const { labelColor, valueColor, backgroundColor } = this.props.tile.properties;
    const id = this.props.tile.properties.dataItem.dataItemId;
    const dataItem = this.props.data.dataItemsObject ? this.props.data.dataItemsObject[id] : {label: '', value: 0};
    
    return (
      <div
        className='indicator'
        style={{width: '100%', height: '100%', textAlign: 'center', backgroundColor: `#${backgroundColor}`}}
      >
        <span style={{color: `#${labelColor}`}}>{dataItem.label}</span>
        <br />
        <span style={{color: `#${valueColor}`}}>{dataItem.value.toFixed(2)}</span>
      </div>
    )
  }
}

function mapStateToProps({ data }) {
  return { data }
}

export default connect(mapStateToProps)(Indicator);
