import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getParameterByName } from '../../util';

class TextBox extends Component {
  static defaultProps = {
    data: undefined //{}
  }
  state = {
    time :'' //new Date().toTimeString().substring(0,5)
  }

  componentDidMount() {
    this.timeInterval = setInterval(() => this.setState({time:new Date().toTimeString().substring(0,5)}),30000)
  }

  componentWillUnmount() {
    clearInterval(this.timeInterval);
  }


  substitutions = (text) => {
    const { data } =  this.props;
    try {
      return eval('`'+text+'`');
    } catch(e) {
      return text;
    }
  }

  render () {
    const { properties } = this.props.tile;
    return (
      <section style={{width: '100%', height: '100%', position: 'relative'}} >
        <div dangerouslySetInnerHTML={{__html: this.substitutions(properties.text)}}>
        </div>
      </section>
    )
  }
}

function mapStateToProps({ data }) {
	return { data }
}

export default connect(mapStateToProps)(TextBox);
