import React, { Component } from 'react';
import { connect } from 'react-redux';
import axios from 'axios';
import { keys } from '../../util/keys';
import { getParameterByName } from '../../util';
//import { dummyData } from '../../util/dummy-data';
import { FiMove, FiRefreshCw } from "react-icons/fi";
import { ScaleLoader } from 'react-spinners';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import variables from '../../styles/variables.scss';

class MultiAxesLive extends Component {
	state = {
		loading: true
	}

	static defaultProps = {}

	chartInit = async () => {
			const { id, properties } = this.props.tile;
			am4core.options.commercialLicense = true;
			this.chart = am4core.create(id, am4charts.XYChart);
			this.chart.exporting.menu = new am4core.ExportMenu();
			this.chart.exporting.menu.verticalAlign = 'bottom';
			this.chart.exporting.menu.align = 'left';
			// data will come from /api/HistoricalDataItems based on selected data items
			try {
				this.chart.data = await this.fetchData();
			} catch(e) {
				console.log(e);
				this.setState({loading: 'failed'});
				this.chart.dispose();
				return
			}
			// Create axes
			const dateAxis = this.chart.xAxes.push(new am4charts.DateAxis());
			//dateAxis.baseInterval = {"timeUnit": 'minute', "count": 1};
			dateAxis.renderer.minGridDistance = 100;
			dateAxis.renderer.grid.template.location = 0.5;
			dateAxis.startLocation = 0.5;
			dateAxis.endLocation = 0.5;
			// loop through based on dataitem this.props.
			// or use props from new field
			this.chart.cursor = new am4charts.XYCursor();
			this.chart.cursor.behavior = "panXY";
			this.chart.cursor.xAxis = dateAxis;
			//this.chart.cursor.snapToSeries = series;
			// create y Axis
			if (properties.axes) {
				for (let i = 0; i < properties.axes.length; i++) {
					const opposite = i % 2 === 0 ? false : true;
					this.createAxes(properties.axes[i], opposite);
				}
			} else {
				this.chart.yAxes.push(new am4charts.ValueAxis());
			}
			// Create series
			if(properties.series) {
				for (let i = 0; i < properties.series.length; i++) {
					const name = properties.series[i].dataItem ? properties.series[i].dataItem.label : `plot${i}`;
					const field = properties.series[i].dataItem ?
						`${properties.series[i].dataItem.assetId}-${properties.series[i].dataItem.dataItemId}` :
						`field${i}`;
					try {
						this.createSeries(field, 'timestamp', name, properties.series[i]);
					} catch(e) {
						console.log(e);
					}
				}
			} else {
				this.createSeries('value', 'date', 'default', this.chart.yAxes.values[0])
			}
			// Create a horizontal scrollbar with previe and place it underneath the date axis
			this.chart.scrollbarX = new am4core.Scrollbar();
			this.chart.scrollbarX.parent = this.chart.bottomAxesContainer;
			this.chart.legend = properties.legend ? new am4charts.Legend() : null;
			//Title
			const Title = this.chart.titles.create();
			Title.text = properties.title !== '' ? properties.title : '';
			Title.marginBottom = 5;
			this.setState({loading: false});
	}

	fetchData = async () => {
		const { start, liveStart, fixedStart, timeType, end, series } = this.props.tile.properties;
		const assetId = getParameterByName('assetId');
    const times ={
      five: new Date(new Date() - (1000*60*5)).toJSON(),
      ten: new Date(new Date() - (1000*60*10)).toJSON(),
      halfhour: new Date(new Date() - (1000*60*60/2)).toJSON(),
      hour: new Date(new Date() - (1000*60*60)).toJSON()
    }

    const startTime = start ? times[start] : timeType === 'live' ? times[liveStart] : fixedStart;
		const endTime = timeType ? timeType === 'live' ? new Date().toJSON() : end : new Date().toJSON();
		const url = '/api/HistoricalDataItems';
		const data = series ? series.map(di => {
			return {
				assetId: assetId,
				dataItemId: di.dataItem ? di.dataItem.dataItemId : 0
			}
		}) : {};
		try {
			const request = await axios({
				url: url,
				baseURL: keys.BASE_URL,
				method: 'POST',
				timeout: 5000,
				headers: {
		      "Content-Type": "application/json",
		      "Authorization": keys.TOKEN,
		    },
				data: {
					"Name": "request",
					"DataItems" : data,
					"StartTime": startTime,
	        "EndTime": endTime,
	        "Format":{"Type":"DataItemChart"}
				}
			});
			if(request.data.length !== 0){
				return request.data.map((di) => {
					return {
						...di,
						timestamp: new Date(di.timestamp)
					}
				});
			} else {
				return [{timestamp: new Date(), value: 0}];
			}
		} catch(e) {
			return [{timestamp: new Date(), value: 0}];
		}
	}

	createSeries = (field, dateX, name, plot) => {
		const { timeType } = this.props.tile.properties;
		const series = this.chart.series.push(new am4charts.LineSeries());
		series.dataFields.valueY = field;
		series.dataFields.dateX = dateX;
		series.strokeWidth = 3; // 3px
		series.name = name;
		series.yAxis = plot.axis ? this[plot.axis.label]: plot;
		series.tensionX = 0.85;
		series.minBulletDistance = 15;
		if(plot.color && plot.color !== '') {
			series.stroke = am4core.color(`#${plot.color}`);
			series.fill = series.stroke;
		}
		// Drop-shaped tooltips
		series.tooltipText = `${name}: {${field}}`//"{value}"
		series.tooltip.background.cornerRadius = 20;
		series.tooltip.background.strokeOpacity = 0;
		series.tooltip.pointerOrientation = "vertical";
		series.tooltip.label.minWidth = 40;
		series.tooltip.label.minHeight = 40;
		series.tooltip.label.textAlign = "middle";
		series.tooltip.label.textValign = "middle";
		// Make bullets grow on hover
		const bullet = series.bullets.push(new am4charts.CircleBullet());
		bullet.circle.strokeWidth = 2;
		bullet.circle.radius = 4;
		bullet.circle.fill = am4core.color("#fff");
		const bullethover = bullet.states.create("hover");
		bullethover.properties.scale = 1.3;

		if(timeType === 'live') {
			// bullet that moves along the end of the graph
			const endBullet = series.createChild(am4charts.CircleBullet);
			endBullet.circle.radius = 3;
			endBullet.fillOpacity = 1;
			//endBullet.fill = this.chart.colors.getIndex(0);
			endBullet.isMeasured = true;
			series.events.on("validated", function() {
				endBullet.moveTo(series.dataItems.last.point);
				endBullet.validatePosition();
			});
		}
	}

	createAxes = (axis, opposite) => {
		this[axis.label] = this.chart.yAxes.push(new am4charts.ValueAxis());
		this[axis.label].title.text = axis.label !== '' ? axis.label : '';
		this[axis.label].renderer.line.strokeOpacity = 1;
	  this[axis.label].renderer.line.strokeWidth = 2;
	  this[axis.label].renderer.grid.template.disabled = true;
		this[axis.label].renderer.opposite = opposite;
		this[axis.label].min = axis.min !== '' ? +axis.min : null;
		this[axis.label].max = axis.max !== '' ? +axis.max : null;
		if(axis.min !== '' && axis.max !== '') {
			this[axis.label].strictMinMax = true;
		}
	}

	async componentDidMount() {
		await this.chartInit();
	}

	componentWillUnmount() {
		if (this.chart) {
 		  this.chart.dispose();
 	  }
	}

	componentDidUpdate(prevProps) {
		const { properties } = this.props.tile;
		const { dataItems } = this.props.data;
		if(prevProps.tile.properties !== properties) {
			this.chart.dispose();
			this.chartInit();
		}
		if(properties.series && dataItems && properties.timeType === 'live') { //this.chart.length !== 0
			const newData = {};
			for (let i = 0; i < properties.series.length; i++) {
				try{
					const di = properties.series[i].dataItem;
					newData[`${di.assetId}-${di.dataItemId}`] = dataItems.find(el => el.label === di.label) ? dataItems.find(el => el.label === di.label).value : 0
				} catch(e) {
					console.log(e);
				}
			}
			newData.timestamp = new Date();
			this.chart.addData(newData);
		}
	}

	render() {
		const { id } = this.props.tile;
		const { loading } = this.state;
		const visible = loading ? 'block' : 'none';
		return ( < >
			<div style={{position: 'absolute', top: '40%', left:'50%', transform: 'translateX(-50%)', display:loading === 'failed' ? 'block' : 'none'}}>
				Something went wrong, try refreshing this tile
			</div>
			<div style={{position: 'absolute', top: '40%', left:'50%', transform: 'translateX(-50%)', display:visible}}>
				<ScaleLoader
					sizeUnit={'px'}
					size={30}
					color={variables.fontColor}
				/>
			</div>
			<div style={{width: "100%", height: "100%", position: 'relative'}} >
				<span
          style={{ position: 'absolute', top: '5px', left: '40px', zIndex: 1000, cursor:'grab' }}
        >
					<span title='Click and hold to move tile'>
						<FiMove style={{height:'1rem'}} />
					</span>
					<span title='Click to refresh graph'>
						<FiRefreshCw
							style={{height:'1rem', marginLeft:'.75rem', cursor:'pointer'}}
							onClick={() => {this.setState({loading:true});this.chartInit();}}
						/>
					</span>
        </span>
				<div
					id={id}
					style={{width: "100%", height: "96%", position: 'absolute', bottom: '0'}}
					onMouseDown={e => e.stopPropagation()}
				>
				</div>
			</div>
		</>)
	}
}

function mapStateToProps({ data }) {
	return { data }
}

export default connect(mapStateToProps)(MultiAxesLive);
