import React, { Component } from 'react';
import { connect } from 'react-redux';
import { InputText } from 'primereact/inputtext';
import { Dropdown } from 'primereact/dropdown';
import { ColorPicker } from 'primereact/colorpicker';

class GaugeForm extends Component {
  state = {
    dataItem: null,
    name: '',
    min : '',
    max: '',
    unit: '',
    band1: null,
    band1End: '',
    band2End: '',
    band2: null,
    band3: null
  }

  dataItems = this.props.data.dataItems;

  componentDidMount() {
    // populates fields when editing
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem, name, min, max, unit, band1, band1End, band2, band2End, band3 } = tile.properties;
      const newDi = this.dataItems.filter((di) => di.label === dataItem.label);
      this.setState({
        dataItems: this.dataItems,
        dataItem: newDi[0],
        name,
        min,
        max,
        unit,
        band1,
        band1End,
        band2,
        band2End,
        band3
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { name, dataItem, min, max, unit, band1, band1End, band2, band2End, band3 } = this.state;
    const { dataItems } = this;
    return (
      <div className='component-form'>
          <Dropdown
            style={{width:'100%'}}
            optionLabel={'label'}
            name='dataItem'
            value={dataItem}
            onChange={this.handleChange}
            options={dataItems}
            placeholder='Select a data item'
            filter={true}
            required={true}
          />
        <span className='p-float-label'>
          <InputText
            value={name}
            type='text'
            onChange={this.handleChange}
            name='name'
          />
          <label htmlFor='name'>Title</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={unit}
            type='text'
            onChange={this.handleChange}
            name='unit'
          />
          <label htmlFor='unit'>Unit</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={min}
            type='number'
            onChange={this.handleChange}
            name='min'
            required
          />
          <label htmlFor='min'>Min*</label>
        </span>
        <span className='p-float-label'>
          <InputText
            value={max}
            type='number'
            onChange={this.handleChange}
            name='max'
            required
          />
          <label htmlFor='max'>Max*</label>
        </span>

        <div className='color-picker' style={{marginBottom:'.25rem'}} >
          <span className='color-picker-label'>Band 1:</span>
          <ColorPicker
            value={band1}
            name='band1'
            onChange={this.handleChange}
            defaultColor='000000'
          />
        <span className='color-picker-color'>{band1}</span>
        </div>
        <div className='dialgauge-band-lengths'>
          <span className=''>{min === '' ? 'min to ' : `${min} to `}</span>
          <InputText
            value={band1End}
            style={{width:'50%'}}
            type='number'
            onChange={this.handleChange}
            name='band1End'
            placeholder='Band 1 End*'
            required
          />
        </div>
        <div className='color-picker' style={{marginBottom:'.25rem'}}>
        <span className='color-picker-label'>Band 2:</span>
        <ColorPicker
          value={band2}
          name='band2'
          onChange={this.handleChange}
          defaultColor='000000'
        />
      <span className='color-picker-color'>{band2}</span>
        </div>
        <div className='dialgauge-band-lengths'>
          <span className=''>{band1End === '' ? 'Band 1 End to ' : `${band1End} to `}</span>
              <InputText
                value={band2End}
                style={{width:'50%'}}
                type='number'
                onChange={this.handleChange}
                name='band2End'
                placeholder='Band 2 End*'
                required
              />
        </div>
        <div className='color-picker' style={{marginBottom:'.25rem'}}>
          <span className='color-picker-label'>Band 3:</span>
          <ColorPicker
            value={band3}
            name='band3'
            onChange={this.handleChange}
            defaultColor='000000'
          />
        <span className='color-picker-color'>{band3}</span>
        </div>
        <div className='dialgauge-band-lengths'>
          <span className=''>{band2End === '' ? 'Band 2 End to ' : `${band2End} to `}</span>
          <span className=''> {max === '' ? 'max' : max} </span>
        </div>
        <p style={{fontSize: '12px', marginTop: 0, marginBottom: '.5rem'}}>
          {'Band colors will default if not specifically selected'}
        </p>
    </div>
    );
  }
}

function mapStateToProps({data, ui}) {
  return {data, ui}
}

export default connect(mapStateToProps)(GaugeForm);

