import React from 'react'
import {Responsive, WidthProvider} from 'react-grid-layout';
import { connect } from "react-redux";
import * as actions from '../actions';
import Tile from '../containers/Tile';

const ResponsiveGridLayout = WidthProvider(Responsive);

class Main extends React.Component {
  renderGrids = (tab) => {
    const { tiles, layouts, activeTab, ui } = this.props;
    const display = activeTab.index === tab.index ? 'visible' : 'hidden'
    const height = activeTab.index === tab.index ? '' : 0
    return (
      <div style={{visibility: display, height: height}} key={tab.index}>
        <ResponsiveGridLayout
          style={{visibility:display, height: height}}
          key={tab.index}
          breakpoints={{lg: 1200, md: 996, sm: 768, xs: 480, xxs: 1}}
          cols={{lg: 96, md: 96, sm: 48, xs: 24, xxs: 1}}
          rowHeight={10}
          layouts={layouts[tab.index]}
          onLayoutChange={(layout, layouts) => this.handleLayoutChange(layout, layouts, tab)}
        >
          {tiles[tab.index] && tiles[tab.index].map(this.renderTile)}
        </ResponsiveGridLayout>
      </div>
    )
  }

  renderTile = (tile) => {
    const { ui } = this.props;
    const noResize = ui.viewOnly ? 'no-resize' : '';
    return (
      <div key={tile.id} data-grid={tile.dataGrid} className={noResize} >
        <Tile tile={tile} toggleModal={this.toggleModal}/>
      </div>
    )
  }

  handleLayoutChange = (layout, layouts, tab) => {
    this.props.saveLayouts(layouts, tab)
  }

  render () {
    const { tabs } = this.props;
    return ( < >
      {tabs && tabs.map(this.renderGrids)}
      </>
    )
  }
}

function mapStateToProps({ activeTab, layouts, tiles, ui, tabs }) {
  return { activeTab, layouts, tiles, ui, tabs }
}

export default connect(mapStateToProps, actions)(Main);

