import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Dropdown } from 'primereact/dropdown'

const tableOptions = [
  {label:'DataItems', value:'dataItems'},
  {label:'Files', value:'files'},
  {label:'Alarms', value:'alarms'},
  {label:'Events', value:'events'},
]

class TableForm extends Component {
  state = {
    option: ''
  }

  dataItems = this.props.data.dataItems;

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    const value = e.target.value ? e.target.value : e.target.checked;
    this.setState({[e.target.name]: value}, () =>{
      addData(this.state);
    });
  }

  render () {
    const { option } = this.state;
    return (
      <section className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          name='option'
          value={option}
          options={tableOptions}
          optionLabel='label'
          onChange={this.handleChange}
          placeholder='Choose Table Type'
          required
          filter
        />
      </section>
    )
  }
}

function mapStateToProps({data}) {
  return {data};
}

export default connect(mapStateToProps)(TableForm);
