import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Dropdown } from 'primereact/dropdown';
import { ColorPicker } from 'primereact/colorpicker';

class IndicatorForm extends Component {
  state = {
    dataItem: undefined,
    labelColor: '',
    valueColor: '',
    backgroundColor: ''
  }

  static defaultProps = {}

  dataItems = this.props.data.dataItems;

  componentDidMount() {
    const { tile } = this.props.ui.modal;
    if(tile) {
      const { dataItem } = tile.properties;
      const newDi = this.dataItems.filter((di) => di.label === dataItem.label);
      this.setState({
        dataItem: newDi[0]
      })
    }
  }

  handleChange = (e) => {
    e.preventDefault();
    const {addData} = this.props;
    this.setState({[e.target.name]: e.target.value}, () =>{
      addData(this.state);
    });
  }

  render() {
    const { dataItem, labelColor, valueColor, backgroundColor } = this.state;
    return (
      <section className='component-form'>
        <Dropdown
          style={{width:'100%'}}
          optionLabel={'label'}
          name='dataItem'
          value={dataItem}
          onChange={this.handleChange}
          options={this.dataItems}
          placeholder='Select a data item'
          filter={true}
          required={true}
        />
        <div className='color-picker' style={{marginBottom:'.25rem'}} >
          <span className='color-picker-label' style={{width:'100px', display: 'inline-block'}}>Label Color</span>
          <ColorPicker
            value={labelColor}
            name='labelColor'
            onChange={this.handleChange}
            defaultColor='000000'
          />
          <span className='color-picker-color' >{labelColor}</span>
        </div>
        <div className='color-picker' style={{marginBottom:'.25rem'}} >
          <span
            className='color-picker-label'
            style={{width:'100px', display: 'inline-block'}}
          >Value Color
          </span>
          <ColorPicker
            value={valueColor}
            name='valueColor'
            onChange={this.handleChange}
            defaultColor='000000'
          />
        <span className='color-picker-color'>{valueColor}</span>
        </div>
        <div className='color-picker' style={{marginBottom:'.25rem'}} >
          <span className='color-picker-label' style={{width:'100px', display: 'inline-block'}} >Background</span>
          <ColorPicker
            value={backgroundColor}
            name='backgroundColor'
            onChange={this.handleChange}
            defaultColor='000000'
          />
        <span className='color-picker-color'>{backgroundColor}</span>
        </div>
      </section>
    )
  }
}

function mapStateToProps({ data, ui }) {
  return { data, ui }
}

export default connect(mapStateToProps)(IndicatorForm);
