const data = (state={}, action) => {
  switch(action.type) {
    case 'CURRENT_DATA': {
      if(action.payload.status === 200) {
        const dataItems = action.payload.data;
        const dataItemsObject = dataItems.reduce((obj, di) => {
          obj[di.dataItemId] = {...di};
          return obj;
        }, {});
        return {
          ...state,
          dataItems: dataItems,
          dataItemsObject: dataItemsObject

        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          dataItems: null
        };
      }
    }
    case 'ATTRIBUTES_DATA': {
      if(action.payload.status === 200) {
        const attributes = action.payload.data;
        const attributesObject = attributes.reduce((obj, attr) => {
          obj[attr.attributeName] = {...attr};
          return obj;
        }, {});
        return {
          ...state,
          attributes: attributes,
          attributesObject: attributesObject

        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          attributes: null
        };
      }
    }
    case 'ASSET_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          asset: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          asset: null
        };
      }
    }
    case 'ASSET_DEFINITION_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          assetDef: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          assetDef: null
        };
      }
    }
    default:
      return state;
  }
}

export default data;
